import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Edit extends Component {

    constructor(props) {
        super(props);
        this.state = {
            contact: {}
        };
    }

    componentDidMount() {
        axios.get('/contacts/'+this.props.match.params.id)
            .then(res => {
            this.setState({ contact: res.data });
        console.log(this.state.contact);
    });
    }

    onChange = (e) => {
    const state = this.state.contact
    state[e.target.name] = e.target.value;
    this.setState({contact:state});
}

onSubmit = (e) => {
    e.preventDefault();

    const { firstName, lastName, userName, passWord } = this.state.contact;

    axios.put('/contacts/'+this.props.match.params.id, { firstName, lastName, userName, passWord })
        .then((result) => {
        this.props.history.push("/show/"+this.props.match.params.id)
    });
}

render() {
    return (
        <div class="container">
        <div class="panel panel-default">
        <div class="panel-heading">
        <h3 class="panel-title">
        EDIT Contact
    </h3>
    </div>
    <div class="panel-body">
        <h4><Link to={`/show/${this.state.contact.id}`}><span class="glyphicon glyphicon-eye-open" aria-hidden="true"></span> Contact List</Link></h4>
    <form onSubmit={this.onSubmit}>


        <div class="form-group">
        <label for="name">FirstName:</label>
    <input type="text" class="form-control" name="firstName" value={this.state.contact.firstName} onChange={this.onChange} placeholder="First Name" />
        </div>
        <div class="form-group">
        <label for="name">LastName:</label>
    <input type="text" class="form-control" name="lastName" value={this.state.contact.lastName} onChange={this.onChange} placeholder="Last Name" />
        </div>
        <div class="form-group">
        <label for="name">UserName:</label>
    <input type="text" class="form-control" name="userName" value={this.state.contact.userName} onChange={this.onChange} placeholder="User Name" />
        </div>
        <div class="form-group">
        <label for="name">Password:</label>
    <input type="text" class="form-control" name="passWord" value={this.state.contact.passWord} onChange={this.onChange} placeholder="Password" />
        </div>


        <button type="submit" class="btn btn-default">Update</button>
        </form>
        </div>
        </div>
        </div>
);
}
}

export default Edit;
