import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Show extends Component {

    constructor(props) {
        super(props);
        this.state = {
            contact: {}
        };
    }

    componentDidMount() {
        axios.get('/contacts/'+this.props.match.params.id)
            .then(res => {
            this.setState({ contact: res.data });
        console.log(this.state.contact);
    });
    }

    delete(id){
        console.log(id);
        axios.delete('/contacts/'+id)
            .then((result) => {
            this.props.history.push("/")
        });
    }

    render() {
        return (
            <div class="container">
            <div class="panel panel-default">
            <div class="panel-heading">
            <h3 class="panel-title">
            Contact Details
        </h3>
        </div>
        <div class="panel-body">
            <h4><Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Contacts List</Link></h4>
        <dl>

        <dt>First Name:</dt>
        <dd>{this.state.contact.firstName}</dd>
        <dt>Last Name:</dt>
        <dd>{this.state.contact.lastName}</dd>
        <dt>User Name:</dt>
        <dd>{this.state.contact.userName}</dd>
        <dt>Password:</dt>
        <dd>{this.state.contact.passWord}</dd>


        </dl>
        <Link to={`/edit/${this.state.contact.id}`} class="btn btn-success">Edit</Link>&nbsp;
            <button onClick={this.delete.bind(this, this.state.contact.id)} class="btn btn-danger">Delete</button>
            </div>
            </div>
            </div>
    );
    }
}

export default Show;