import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Create extends Component {

    constructor() {
        super();
        this.state = {
            // name: '',
            firstName: '',
            lastName: '',
            userName: '',
            passWord: '',


            // address: '',
            // city: '',
            // phone: '',
            // email: ''
        };
    }
    onChange = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value;
    this.setState(state);
}

onSubmit = (e) => {
    e.preventDefault();

    const { firstName, lastName, userName, passWord } = this.state;

    axios.post('/contacts', { firstName, lastName, userName, passWord })
        .then((result) => {
        this.props.history.push("/")
    });
}

render() {
    const { firstName, lastName, userName, passWord } = this.state;
    return (
        <div class="container">
        <div class="panel panel-default">
        <div class="panel-heading">
        <h3 class="panel-title">
        ADD CONTACT
    </h3>
    </div>
    <div class="panel-body">
        <h4><Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Contacts List</Link></h4>
    <form onSubmit={this.onSubmit}>


        <div class="form-group">
        <label for="isbn">FirstName:</label>
    <input type="text" class="form-control" name="firstName" value={firstName} onChange={this.onChange} placeholder="First Name" />
        </div>
        <div class="form-group">
        <label for="isbn">LastName:</label>
    <input type="text" class="form-control" name="lastName" value={lastName} onChange={this.onChange} placeholder="Last Name" />
        </div>
        <div class="form-group">
        <label for="isbn">UserName:</label>
    <input type="text" class="form-control" name="userName" value={userName} onChange={this.onChange} placeholder="User Name" />
        </div>
        <div class="form-group">
        <label for="isbn">Password:</label>
    <input type="text" class="form-control" name="passWord" value={passWord} onChange={this.onChange} placeholder="Password" />
        </div>


        <button type="submit" class="btn btn-default">Submit</button>
        </form>
        </div>
        </div>
        </div>
);
}
}

export default Create;
